body {font-family: Arial, Helvetica, sans-serif;}
* {box-sizing: border-box;}

/* Button used to open the contact form - fixed at the bottom of the page */
.open-button {
  background-color: #555;
  color: white;
  padding: 16px 20px;
  border: none;
  cursor: pointer;
  opacity: 0.8;
  position: fixed;
  bottom: 23px;
  right: 28px;
  width: 280px;
}

/* The popup form - hidden by default */
.form-popup {
  display: block;
  position: fixed;
  border: 3px solid #f1f1f1;
  z-index: 9;
}

.middle {
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.bottom-right {
  bottom: 0;
  right: 15px;
}

.hide-button {
  color: #2f51d9;
  background-color: white;
  position: absolute;
  top: 5px;
  right: 5px;
  border: 3px;
  font-size: 15px;
}

.show-button {
  color: white;
  background-color: #2f51d9;
  position: absolute;
  top: 5px;
  right: 5px;
  border: 3px;
  font-size: 15px;
}

.logout-button {
  color: white;
  background-color: #00b07e;
  position: absolute;
  top: 5px;
  right: 5px;
  border: 3px;
  font-size: 15px;
}

/* Add styles to the form container */
.form-container {
  max-width: 300px;
  padding: 10px;
  background-color: white;
}

/* Full-width input fields */
.form-container input[type=text], .form-container input[type=password] {
  width: 100%;
  padding: 15px;
  margin: 5px 0 22px 0;
  border: none;
  background: #f1f1f1;
}

/* When the inputs get focus, do something */
.form-container input[type=text]:focus, .form-container input[type=password]:focus {
  background-color: #ddd;
  outline: none;
}

/* Set a style for the submit/login button */
.form-container .btn {
  background-color: #04AA6D;
  color: white;
  padding: 16px 20px;
  border: none;
  cursor: pointer;
  width: 100%;
  margin-bottom:10px;
  opacity: 0.8;
}

.btn-sign-up {
  background-color: #df3d2b;
  color: white;
  padding: 16px 20px;
  border: none;
  cursor: pointer;
  width: 100%;
  margin-bottom:10px;
  opacity: 0.8;
}

/* Add a red background color to the cancel button */
.form-container .cancel {
  background-color: red;
}

/* Add some hover effects to buttons */
.form-container .btn:hover, .open-button:hover {
  opacity: 1;
}

.center{
  text-align: center;
}

.error-msg{
  margin-top: 5px;
  color: red;
}